<!--热门众测产品列表 -->
<template>
    <div class="hotProduct">
        <ul class="productBox">
            <li v-for="(item,index) in resData" :key="index" @click="enter(item.id)">
                <div class="itemBox">
                    <img :src="item.imgUrl" class="itemImg">
                    <div class="itemInfo">
                        <p class="itemInfo_title">
                            {{item.proName}}
                        </p>
                        
                        <p v-if="item.status==0" class='itemInfo_status status_may'>
                            <span></span>可申请
                        </p>

                        <p v-else-if="item.status==1" class='itemInfo_status status_experience'>
                            <span></span>体验中
                        </p>

                        <p v-else-if="item.status==2" class='itemInfo_status status_over'>
                            <span></span>已结束
                        </p>

                        <p class='num'>名额 {{item.quota}} </p>
                        <p class='num'>申请人数 {{item.person}}</p>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  props: ['resData'],
  data() {
    return {
    };
  },
  methods:{
    enter(id){
       console.log(id)
    }
  }
};
</script>
<style lang="less" scoped>
@m: 750/375;
.productBox {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  // margin-top: 30px * @m;
}

.itemBox {
  width: 162px * @m;
  height: 250px * @m;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.5);
  border-radius: 6px * @m;
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
  margin-bottom: 20px * @m;
}

.itemBox .itemImg {
  display: block;
  width: 100%;
  height: 108px * @m;
  //   background-image: url("./image/Bitmap.png");
  //   background-repeat: no-repeat;
  //   background-size: cover;
}

.itemInfo {
  width: 100%;
  height: 142px * @m;
  padding: 10px * @m 10px * @m 0 10px * @m;
  box-sizing: border-box;
  border-radius: 0 0 6px * @m 6px * @m;
}

.itemInfo_title {
  font-size: 16px * @m;
  margin-bottom: 10px * @m;
  height: 40px * @m;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.itemInfo .itemInfo_status {
  font-size: 13px * @m;
  display: flex;
  align-items: center;
  margin-bottom: 6px * @m;
  /* color: #64D48C; */
}

.itemInfo .itemInfo_status span {
  font-size: 13px * @m;
  display: inline-block;
  border-radius: 100%;
  width: 6px * @m;
  height: 6px * @m;
  margin: 0 6px * @m 0 2px * @m;
  /* background-color: #64D48C; */
}

.status_may {
  color: #64d48c;
}

.status_may span {
  background-color: #64d48c;
}

.status_experience {
  color: #73b9ec;
}
.status_experience span {
  background-color: #73b9ec;
}

.status_over {
  color: #f58c8c;
}
.status_over span {
  background-color: #f58c8c;
}

.itemInfo .num {
  font-size: 13px * @m;
  color: #c6c6c6;
  margin-bottom: 6px * @m;
}
</style>



