import Vue from 'vue'
import Router from 'vue-router'

// 承载
import testProduct from '../pages/testProduct.vue'
// 主页
import testProductIndex from '../views/test-product.vue'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'testProduct',
    component: testProductIndex
  }]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#hotTestProductList',
  router,
  components: {
    testProduct
  },
  template: '<testProduct/>'
})



